/**
 * @file 
 * @author vaer
 */

import About from '../routes/About';
import Blog from '../routes/Blog';
import Detail from '../routes/Detail/index';
import News from '../routes/News';

export default [
    {
        path: '/about',
        component: About,
        exact: true,
        name: ''
    }, {
        path: '/',
        component: Blog,
        exact: true,
        name: ''
    }, {
        path: '/:date',
        exact: true,
        component: Detail
    }, {
        path: '/news',
        component: News,
        exact: true,
        name: ''
    }
];


